define('pilas-engine-bloques/components/pilas-desafio', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    classNames: ["desafio"],
    nombre: null,
    deshabilitada: false,

    actions: {
      abrir: function abrir() {
        this.sendAction("onSelect", this.get("nombre"));
      }
    }
  });

});